import * as React from 'react';
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import sectionsHelper from '../Helpers/sectionsHelper';
import { useMediaQuery } from '@mui/material';

export default function SectionGroup(props) {

    const SectionGroupContainer = {
        borderBottom: '1px solid #8c8c8c',
    };

    const buttonStyleMD = {
        color: '#000000',
        borderRadius: '0px',
        borderRight: '1px solid #8c8c8c',
        fontFamily: "Trebuchet MS, Lucida Grande, Lucida Sans Unicode, Lucida Sans, Tahoma, sans-serif",
        textTransform: 'none',
        backgroundImage: 'linear-gradient(to bottom, #f0f0f0, #D3D3D3)',
    }

    const ButtonGroupStyleMD = {

    }

    const buttonStyleSM = {
        color: '#000000',
        borderRadius: '0px',
        borderBottom: '1px solid #8c8c8c',
        fontFamily: "Trebuchet MS, Lucida Grande, Lucida Sans Unicode, Lucida Sans, Tahoma, sans-serif",
        textTransform: 'none',
        backgroundImage: 'linear-gradient(to bottom, #f0f0f0, #D3D3D3)',
    }

    const buttomGroupStyleSM = {
        width: '100%',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'stretch',
    }

    // Loading different component for less than 600px size screen
    const isMd = useMediaQuery('(min-width:600px)');

    return (
        <div style={SectionGroupContainer}>
            {
                isMd ? (
                    <ButtonGroup variant="text" aria-label="text button group" style={ButtonGroupStyleMD}>
                        {
                            Object.keys(sectionsHelper).map((key) => {
                                return (
                                    <Button onClick={() => window.open(sectionsHelper[key].url, "_self")} style={buttonStyleMD}>{key}</Button>
                                );
                            })
                        }
                    </ButtonGroup>
                ) : (
                    <ButtonGroup orientation="vertical" variant="text" aria-label="text button group" style={buttomGroupStyleSM}>
                        {
                            Object.keys(sectionsHelper).map((key) => {
                                return (
                                    <Button onClick={() => window.open(sectionsHelper[key].url, "_self")} style={buttonStyleSM}>{key}</Button>
                                );
                            })
                        }
                    </ButtonGroup>
                )
            }
        </div>
    );
}