import Carousel from 'react-material-ui-carousel'
import { Paper, Button } from '@mui/material'
import SupremeCourt from '../../Images/1.jpg'
import LegalDesk from '../../Images/2.jpg'
import HighCourt from '../../Images/3.jpg'

export default function ImageCarousel(props) {
    var items = [
        {
            name: "Indian Supreme Court",
            imageUrl: SupremeCourt,
        },
        {
            name: "Legal advice desk",
            imageUrl: LegalDesk,
        },
        {
            name: "Gujarat High court",
            imageUrl: HighCourt,
        },
    ]

    return (
        <Carousel
            next={ (next, active) => {  } }
            prev={ (prev, active) => {  } }
            animation = "slide"            
        >
            {
                items.map((item, i) => <Item key={i} item={item} />)
            }
        </Carousel>
    )
}

function Item(props) {
    const itemStyle = {
        margin:'8px', 
        borderRadius: '8px',
    }
    return (
        <Paper style={itemStyle}>
            <img
                src={props.item.imageUrl}
                alt={props.item.name}
                style={{ width: '100%', borderRadius: '8px' }}
            />
        </Paper>
    )
}